.button {
    display: flex;
    align-items: center;
    justify-content: center;
    user-select: none;
    touch-action: manipulation;
    position: relative;
    font-family: $title;
    font-size: 22px;
    line-height: 29px;
    vertical-align: middle;
    text-align: center;
    white-space: normal;
    word-wrap: normal;
    border: none;
    background-color: $black;
    border: none;
    border-radius: 45px;
    color: #ffffff;
    cursor: pointer;
    padding-left: 80px;
    padding-right: 80px;
    padding-top: 22px;
    padding-bottom: 19px;
    cursor: pointer;
    transition: background-color .3s;
    white-space: nowrap;
    text-decoration: none;
    letter-spacing: 0.02em;

    &:hover {} }


