.header {
    font-size: 22px;
    line-height: 21px;
    font-family: $bold;
    color: $black-text;
    padding-top: 73px;

    &__container {
        @include wrapper(); }

    &__top-bar {
        @include row-flex();
        justify-content: space-between; }

    &__info {
        display: flex;
        justify-content: space-between;
        @include col();
        @include size(6); }

    &__links {
        @include col();
        @include size(5);
        justify-content: space-between; }

    &__info-item {
        margin-top: 0;
        margin-bottom: 0;

        &--inner {
            display: block; }

        &--green {
            color: $green; } }

    &__links {
        display: flex;
        align-items: flex-start; }

    &__link {
        color: $black-text;
        text-decoration: none;
        font-size: 22px;
        line-height: 22px;

        &--yellow {
            position: relative;
            z-index: 0;
            background-color: $yellow; } }





    &__offer {
        display: flex;
        padding-top: 132px;
        display: flex;
        padding-bottom: 220px; }

    &__offer-message {
        flex-basis: 50%;
        max-width: 50%;
        padding-right: 55px;
        flex-grow: 0;
        flex-shrink: 0;
        box-sizing: border-box; }

    &__offer-logo {
        img {
            display: block; } }


    &__offer-image {
        position: relative;
        flex-basis: 50%;
        max-width: 50%;
        flex-grow: 0;
        flex-shrink: 0;

        img {
            position: absolute;
            left: 0;
            top: -68px; } }

    &__offer-title {
        font-size: 48px;
        line-height: 64px;
        margin-top: 80px;
        margin-bottom: 0;
        font-family: $title;
        max-width: 525px;

        &--highlight {
            display: block; }

        &--green {
            position: relative;
            z-index: 0;

            &::before {
                content: '';
                background-color: $green-light;
                position: absolute;
                left: 0;
                top: 11px;
                width: 420px;
                height: 35px;
                z-index: -1; } }


        &--right {
            text-align: right; } }



    &__offer-text {
        font-size: 28px;
        line-height: 27px;
        font-family: $regular;
        max-width: 545px;
        margin-top: 10px;
        margin-bottom: 0; }

    &__offer-button {
        margin-top: 47px; } }
