@mixin font-face($font-name, $path) {
  @font-face {
    font-family: quote($font-name);
    src: url(#{$path}.woff2) format("woff2");
    font-weight: normal;
    font-style: normal;
    font-display: swap; } }

@include font-face('NeueMachinaBold', '../fonts/NeueMachina-Bold');
@include font-face('NeueMachinaRegular', '../fonts/NeueMachina-Regular');
@include font-face('BodyGrotesqueLargeTrialBold', '../fonts/BodyGrotesqueLargeTrial-Bold');
